import React from 'react';
import { connect } from 'react-redux';
import { Strings } from '../Assets/Language/Strings';
import { Stories } from '../Components/Stories';

const SuccessStories = ({language}) => {

    const {SuccessStories: {title, projects}} = Strings[language];

    return (
        <div className="center">
            <h2 className="animate__animated animate__bounceInRight">{title}</h2>
            <hr className="animate__animated animate__bounceInRight" />
            <div className="listaContainer animate__animated animate__backInLeft">
                {
                    projects.map((val,i) => (
                        <Stories 
                            description={val.resumen}
                            img={val.img}
                            title={val.title}
                            key={"Stories"+i}
                        />
                    ))
                }
            </div>
        </div>
    )
}

const mapStateToProps = (state) => ({
    language: state
})

export default connect(mapStateToProps,{})(SuccessStories)